import React, {useEffect, useState} from 'react';
import {useNavigate, useParams} from 'react-router-dom';
import {deletePost, getPost} from 'api/board';
import {Box, Button, Card, CardContent, CircularProgress, Divider, Grid, InputLabel, Stack, Toolbar, Typography, Chip, Avatar, TableContainer, Table, TableRow, TableCell} from '@mui/material';
import {tableCellClasses} from '@mui/material/TableCell';
import {LoadingButton} from '@mui/lab';
import {useSnackbar} from 'notistack';
import CustomChip from 'components/CustomChip';
import SimpleDialog from 'components/SimpleDialog';

const MyIcareDetail = () => {
	// [api 연결] useSnackbar에서 enqueueSnackbar 가져오기
	const {enqueueSnackbar} = useSnackbar();

	// [이벤트 매핑] useNavigate에서 navigate 가져오기
	const navigate = useNavigate();

	//url에서 param 가져오기
	const {id} = useParams();

	// [api 연결] Todo: post(기본값 : null), isDeleteLoading(기본값 : false)을 컴포넌트 State로 선언,(useState)
	// 작성해주세요.
	const [post, setPost] = useState(null);
	const [isDeleteLoading, setDeleteLoading] = useState(false);

	const [open, setOpen] = React.useState(false);
	const [selectedValue, setSelectedValue] = React.useState(null);

	const handleClickOpen = () => {
		setOpen(true);
	};

	const handleClose = (value) => {
		setOpen(false);
		setSelectedValue(value);
	};

	const [apply, setApply] = useState(false);

	useEffect(() => {
		// [api 연결] Todo: 로딩 시 게시글 상세 api 호출 함수 실행
		findPost();
	}, [id]);

	const findPost = async () => {
		// [api 연결] Todo: 게시글 상세 api 호출 후 결과 받기
		//const response = await getPost({id});
		// [api 연결] Todo: post에 api 호출 결과값 매핑
		//setPost(response);
		setPost({});
	};

	const goBackList = () => {
		// [이벤트 매핑] Todo: 게시판 페이지로 이동
		navigate('/board');
	};

	const deleteClick = async () => {
		try {
			// [api 연결] Todo: 중복 api 호출 방지를 위해 isDeleteLoading 상태 true로 변경
			setDeleteLoading(true);

			// [api 연결] Todo: 게시글 삭제 api 호출
			await deletePost({id});

			enqueueSnackbar('게시글이 삭제되었습니다.', {variant: 'success'});

			// [api 연결] Todo: 중복 api 호출 후 delete 버튼 활성화를 위해 isDeleteLoading false로 변경
			setDeleteLoading(false);

			// [이벤트 매핑] Todo: 게시판 페이지로 이동
			goBackList();
		} catch (err) {
			enqueueSnackbar(err, {variant: 'error'});
			// [api 연결] Todo: 중복 api 호출 후 delete 버튼 활성화를 위해 isDeleteLoading false로 변경
			setDeleteLoading(false);
		}
	};

	const updatePost = async () => {
		// [이벤트 매핑] 게시글 수정 페이지로 이동. 상태 값을 같이 전달
		navigate(`/post/update`, {
			state: {
				id: id,
				title: post.title,
				content: post.content,
			},
		});
	};

	const isApplied = () => {
		setApply(true);
	};

	return (
		<>
			<Stack direction='row' spacing={2}>
				<Grid container>
					<Grid item>
						<Button
							variant='contained'
							// [이벤트 매핑] Todo: 목록 버튼을 클릭하면 게시글 목록페이지 페이지로 이동해야 합니다.
							onClick={goBackList}>
							목록
						</Button>
					</Grid>
				</Grid>
				<Grid container justifyContent='flex-end'>
					<Grid item>
						<Button variant='contained' onClick={isApplied} style={{marginRight: 10}}>
							신청
						</Button>
					</Grid>
					<Grid item>
						<Button
							variant='outlined'
							// [이벤트 매핑] Todo: 수정 버튼을 클릭하면 게시글 수정 페이지로 이동해야 합니다.
							onClick={updatePost}
							style={{marginRight: 10}}>
							수정
						</Button>
					</Grid>
					<Grid item>
						<LoadingButton
							variant='contained'
							// [이벤트 매핑] Todo: 삭제 버튼을 클릭하면 게시글 삭제 api가 호출됩니다.
							onClick={deleteClick}
							color='error'
							// [api 연결] Todo: isLoading이 true면 버튼이 비활성화 됩니다. isDeleteLoading을 매핑해주세요.
							loading={isDeleteLoading}
							style={{marginRight: 10}}>
							삭제
						</LoadingButton>
					</Grid>
					{/* <Grid item>
						<LoadingButton
							variant='contained'
							// [이벤트 매핑] Todo: 삭제 버튼을 클릭하면 게시글 삭제 api가 호출됩니다.
							onClick={deleteClick}
							color='error'
							// [api 연결] Todo: isLoading이 true면 버튼이 비활성화 됩니다. isDeleteLoading을 매핑해주세요.
							loading={isDeleteLoading}>
							수락
						</LoadingButton>
					</Grid> */}
				</Grid>
			</Stack>
			<Card sx={{p: 2}} style={{borderRadius: '8px', marginTop: 15}}>
				<Toolbar>
					<Typography sx={{flex: '1 1 100%'}} variant='h4' id='tableTitle' component='div'>
						{/* {post?.title ? post.title : ''} */}
						{'전무룡'}님의 내 아이 돌봄 요청
					</Typography>
				</Toolbar>
				<Divider />
				<CardContent>
					{post ? (
						// <Grid container direction='column' spacing={4}>
						// 	{/* <Grid container justifyContent='flex-end'>
						// 			<InputLabel htmlFor='desc-signup'>
						// 				{'작성자 : '}
						// 				{'전무룡'}
						// 			</InputLabel>
						// 		</Grid> */}
						// 	{/* <Typography variant='body1' aria-label='maximum height' placeholder='내용'>
						// 			{/* {post.content}
						// 		</Typography> */}
						// 	<Grid item>
						// 		<Typography variant='h5' color='textPrimary'>
						// 			시작 일자
						// 		</Typography>
						// 		<Typography variant='body1'>{'2023-07-07 20:00'}</Typography>
						// 	</Grid>
						// 	<Grid item>
						// 		<Typography variant='h5'>종료 일자</Typography>
						// 		<Typography variant='body1'>{'2023-07-07 23:00'}</Typography>
						// 	</Grid>
						// 	<Grid item>
						// 		<Typography variant='h5'>장소</Typography>
						// 		<Typography variant='body1'>{'우리집 방구석'}</Typography>
						// 	</Grid>
						// 	<Grid item>
						// 		<Typography variant='h5'>{'사용자'} 정보</Typography>
						// 		<Stack direction='row'>
						// 			<CustomChip size={1.2} avatar={<Avatar alt='Natacha' src='/avatar4.jpeg' />} label={'김숙자'} onClick={handleClickOpen} />
						// 		</Stack>
						// 	</Grid>
						// </Grid>
						<Grid container spacing={1} mt={2}>
							<TableContainer>
								<Table
									sx={{
										minWidth: 650,
										[`& .${tableCellClasses.root}`]: {
											borderBottom: 'none',
										},
									}}
									aria-label='simple table'>
									<TableRow>
										<TableCell align='right' style={{width: 100}}>
											<Typography variant='subtitle1'>시작일자</Typography>
										</TableCell>
										<TableCell>
											<Typography variant='body1'>{'2023-07-07 20:00'}</Typography>
										</TableCell>
									</TableRow>
									<TableRow>
										<TableCell align='right'>
											<Typography variant='subtitle1'>종료 일자</Typography>
										</TableCell>
										<TableCell>
											<Typography variant='body1'>{'2023-07-07 23:00'}</Typography>
										</TableCell>
									</TableRow>
									<TableRow>
										<TableCell align='right'>
											<Typography variant='subtitle1'>장소</Typography>
										</TableCell>
										<TableCell>
											<Typography variant='body1'>{'우리집 방구석'}</Typography>
										</TableCell>
									</TableRow>
									<TableRow>
										<TableCell align='right'>
											<Typography variant='subtitle1'>{'사용자'} 정보</Typography>
										</TableCell>
										<TableCell>
											<CustomChip size={1.4} avatar={<Avatar alt='Natacha' src='/avatar4.jpeg' />} label={'전무룡'} onClick={handleClickOpen} />
										</TableCell>
									</TableRow>
									<TableRow>
										<TableCell align='right'>
											<Typography variant='subtitle1'>요청사항</Typography>
										</TableCell>
										<TableCell>
											<Typography variant='body1'>훈남만 신청주세요~</Typography>
										</TableCell>
									</TableRow>
									<TableRow>
										<TableCell align='right'>
											<Typography variant='subtitle1'>진행 상태</Typography>
										</TableCell>
										<TableCell>
											<Typography variant='body1'>요청 됨</Typography>
										</TableCell>
									</TableRow>
								</Table>
							</TableContainer>
						</Grid>
					) : (
						<Box sx={{py: 3, minHeight: 560}} style={{textAlign: 'center'}}>
							<CircularProgress />
						</Box>
					)}
				</CardContent>
			</Card>
			{apply ? (
				<Card sx={{p: 2}} style={{borderRadius: '8px', marginTop: 15, backgroundColor: '#FFEFEF'}}>
					<Toolbar>
						<Typography sx={{flex: '1 1 100%'}} variant='h4' id='tableTitle' component='div'>
							{/* {post?.title ? post.title : ''} */}
							{'구영진'}님의 돌봄 신청
						</Typography>
					</Toolbar>
					<Divider
						sx={{bgcolor: '#C9A2A2'}}
						style={{
							border: 'none',
							height: 1,
							margin: 0,
						}}
					/>
					<CardContent>
						{post ? (
							// <Grid container direction='column' spacing={4}>
							// 	{/* <Grid container justifyContent='flex-end'>
							// 			<InputLabel htmlFor='desc-signup'>
							// 				{'작성자 : '}
							// 				{'전무룡'}
							// 			</InputLabel>
							// 		</Grid> */}
							// 	{/* <Typography variant='body1' aria-label='maximum height' placeholder='내용'>
							// 			{/* {post.content}
							// 		</Typography> */}
							// 	<Grid item>
							// 		<Typography variant='h5' color='textPrimary'>
							// 			시작 일자
							// 		</Typography>
							// 		<Typography variant='body1'>{'2023-07-07 20:00'}</Typography>
							// 	</Grid>
							// 	<Grid item>
							// 		<Typography variant='h5'>종료 일자</Typography>
							// 		<Typography variant='body1'>{'2023-07-07 23:00'}</Typography>
							// 	</Grid>
							// 	<Grid item>
							// 		<Typography variant='h5'>장소</Typography>
							// 		<Typography variant='body1'>{'우리집 방구석'}</Typography>
							// 	</Grid>
							// 	<Grid item>
							// 		<Typography variant='h5'>{'사용자'} 정보</Typography>
							// 		<Stack direction='row'>
							// 			<CustomChip size={1.2} avatar={<Avatar alt='Natacha' src='/avatar4.jpeg' />} label={'김숙자'} onClick={handleClickOpen} />
							// 		</Stack>
							// 	</Grid>
							// </Grid>
							<Grid container spacing={1} mt={2}>
								<TableContainer>
									<Table
										sx={{
											minWidth: 650,
											[`& .${tableCellClasses.root}`]: {
												borderBottom: 'none',
											},
										}}
										aria-label='simple table'>
										<TableRow>
											<TableCell align='right' style={{width: 100}}>
												<Typography variant='subtitle1'>돌보미</Typography>
											</TableCell>
											<TableCell>
												<CustomChip size={1.4} avatar={<Avatar alt='Natacha' src='/avatar1.jpeg' />} label={'구영진'} onClick={handleClickOpen} />
											</TableCell>
										</TableRow>
										<TableRow>
											<TableCell align='right'>
												<Typography variant='subtitle1'>요청사항</Typography>
											</TableCell>
											<TableCell>
												<Typography variant='body1'>저 훈남임</Typography>
											</TableCell>
										</TableRow>
									</Table>
								</TableContainer>
							</Grid>
						) : (
							<Box sx={{py: 3, minHeight: 560}} style={{textAlign: 'center'}}>
								<CircularProgress />
							</Box>
						)}
					</CardContent>
					<Grid container direction='row' justifyContent='flex-end' spacing={2}>
						<Grid item>
							<Button variant='contained'>수락</Button>
						</Grid>
						<Grid item>
							<Button variant='contained' color='error'>
								거절
							</Button>
						</Grid>
					</Grid>
				</Card>
			) : null}

			<SimpleDialog selectedValue={selectedValue} open={open} onClose={handleClose} />
		</>
	);
};

export default MyIcareDetail;
